/********************************************************************
 * https://gearheart.io/blog/auto-websocket-reconnection-with-rxjs/ *
 ********************************************************************/

import * as Rx from 'rxjs/Rx';
import {WebSocketSubjectConfig, WebSocketSubject} from 'rxjs/observable/dom/WebSocketSubject';

/// we inherit from the ordinary Subject
export class RxWebSocketSubject<T> extends Rx.Subject<T> {
  private reconnectionObservable: Rx.Observable<number>;
  private wsSubjectConfig: WebSocketSubjectConfig;
  private socket: WebSocketSubject<any>;
  // private connectionObserver: Observer<boolean>;
  public connectionStatus: Rx.BehaviorSubject<boolean>;

  /// by default, when a message is received from the server, we are trying to decode it as JSON
  /// we can override it in the constructor
  defaultResultSelector = (e: MessageEvent) => {
    return JSON.parse(e.data);
  };

  /// when sending a message, we encode it to JSON
  /// we can override it in the constructor
  defaultSerializer = (data: any): string => {
    return JSON.stringify(data);
  };

  constructor(
    private url: string,
    private reconnectInterval = 5000,  /// pause between connections
    private reconnectAttempts = 10,  /// number of connection attempts

    private resultSelector?: (e: MessageEvent) => any,
    private serializer?: (data: any) => string,
    ) {
    super();

    /// connection status
    this.connectionStatus = new Rx.BehaviorSubject<boolean>(false);
    // this.connectionStatus.subscribe((value) => this.connectionObserver.next(value));

    if (!resultSelector) {
      this.resultSelector = this.defaultResultSelector;
    }
    if (!this.serializer) {
      this.serializer = this.defaultSerializer;
    }

    /// config for WebSocketSubject
    /// except the url, here is closeObserver and openObserver to update connection status
    this.wsSubjectConfig = {
      url: url,
      closeObserver: {
        next: (e: CloseEvent) => {
          this.socket = null;
          this.connectionStatus.next(false);
        }
      },
      openObserver: {
        next: (e: Event) => {
          this.connectionStatus.next(true);
        }
      }
    };
    /// we connect
    this.connect();
    /// we follow the connection status and run the reconnect while losing the connection
    this.connectionStatus.subscribe((isConnected) => {
      if (!this.reconnectionObservable && typeof(isConnected) === 'boolean' && !isConnected) {
        this.reconnect();
      }
    });
  }

  public subscribeLambda(argLambda) : void {
    super.subscribe({ next: argLambda });
  }

  connect(): void {
    this.socket = new WebSocketSubject(this.wsSubjectConfig);
    this.socket.subscribe(
      (m) => {
        this.send(m); /// when receiving a message, we just send it to our Subject
      },
      (error: Event) => {
        if (!this.socket) {
          /// in case of an error with a loss of connection, we restore it
          this.reconnect();
        }
      });
  }

  /// reconnection
  reconnect(): void {
    this.reconnectionObservable = Rx.Observable.interval(this.reconnectInterval)
      .takeWhile((v, index) => {
        return index < this.reconnectAttempts && !this.socket
    });
    this.reconnectionObservable.subscribe(
      () => {
        this.connect();
      },
      null,
      () => {
        /// if the reconnection attempts are failed, then we call complete of our Subject and status
        this.reconnectionObservable = null;
        if (!this.socket) {
          this.complete();
          this.connectionStatus.complete();
        }
      });
  }

  /// sending the message
  send(data: any): void {
    this.socket.next(this.serializer(data));
  }
}



